<template>
	<div class="tree-list">
		<!-- 	<div class="tree-head">
			<table>
				<tr>
					<td class="td1">组织部门</td>
				</tr>
			</table>
		</div> -->
		<div id="scrollWrap" class="tree-wrap">
			<div class="tree-body">
				<table v-if="treeDataSource.length > 0">
					<tbody>
						<tr>
							<td>
								<tree-item
									v-for="(model, i) in treeDataSource"
									:key="'root_node_' + i"
									:root="0"
									:num="i"
									@actionFunc="actionFunc"
									@handlerExpand="handlerExpand"
									@handlerChooseModel="handlerChooseModel"
									:nodes="treeDataSource.length"
									:trees.sync="treeDataSource"
									:model.sync="model"
								></tree-item>
							</td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'treeTable',
	props: ['list'],
	data() {
		return {
			isDesc: false,
			treeDataSource: []
		};
	},

	watch: {
		list: {
			handler() {
				this.initTreeData();
			},
			deep: true
		},
		isDesc(nelVal, oldVal) {
			this.$emit('orderByFunc', nelVal);
		}
	},
	computed: {},
	methods: {
		initTreeData() {
			// 这里一定要转化，要不然他们的值监听不到变化
			let tempData = JSON.parse(JSON.stringify(this.list));
			let reduceDataFunc = (data, level) => {
				data.map((m, i) => {
					m.isExpand = !m.isExpand ? false : m.isExpand;
					m.children = m.children || [];
					m.level = level;
					m.bLeft = level === 1 ? 34 : (level - 2) * 16 + 34;
					m.Experience = m.Experience || '无';
					if (m.children.length > 0) {
						reduceDataFunc(m.children, level + 1);
					}
				});
			};
			reduceDataFunc(tempData, 1);
			this.treeDataSource = tempData;
		},
		// 编辑,新增
		actionFunc(m, type) {
			this.$emit('actionFunc', m, type);
		},
		// 展开
		handlerExpand(m) {
			this.$emit('handlerExpand', m);
		},
		handlerChooseModel(m){
			this.$emit('handlerChooseModel', m);
		},
	},
	components: {
		treeItem: () => import('./tree-item.vue')
	},
	mounted() {
		const vm = this;
		vm.$nextTick(() => {
			vm.initTreeData();
		});
	}
};
</script>

<style lang="scss">
.tree-list {
	width: 100%;
	position: relative;
	background: #fff;
	.center {
		text-align: center;
	}
	table {
		width: 100%;
		text-align: center;
		border-collapse: collapse;
		border-spacing: 0;
		td {
			word-break: break-all;
			word-wrap: break-word;
			font-size: 16px;
		}
	}
	.td-border {
		margin-top: 10px;
		border: 1px solid #e8e8e8;
		.leve {
			// 屏蔽双击不能选择文本样式
			-webkit-user-select: none;
			-moz-user-select: none;
			-o-user-select: none;
			user-select: none;
			&:hover {
				background-color: #f7f9ff;
			}
		}
	}
	th,
	td {
		font-weight: 400;
		text-align: left;
	}
	.td1 {
		width: 100%;
		padding-left: 30px;
		border-top: none;
	}
	.td2 {
		width: 33%;
		text-align: center;
		border: 1px solid #e8e8e8;
		border-top: none;
	}
	.td3 {
		width: 120px;
	}
	.td4 {
		width: 220px;
	}
	.td5 {
		width: 220px;
	}
	.td6 {
		width: 33%;
		text-align: center;
		border: 1px solid #e8e8e8;
		border-top: none;
	}
	.p20 {
		padding-left: 20px;
	}
	.leve-1 .td1 {
		padding-left: 30px;
	}
	.leve-2 .td1 {
		padding-left: 46px;
	}
	.leve-3 .td1 {
		padding-left: 62px;
	}
	.leve-4 .td1 {
		padding-left: 78px;
	}
	.leve-5 .td1 {
		padding-left: 90px;
	}
	.leve-6 .td1 {
		padding-left: 106px;
	}
	.leve-7 .td1 {
		padding-left: 122px;
	}
	.leve-8 .td1 {
		padding-left: 138px;
	}
	.leve-9 .td1 {
		padding-left: 154px;
	}
	.td4 {
		.unvisibie {
			visibility: hidden;
		}
		.ellipsis {
			display: block;
			max-width: 200px;
		}
		.iconfont {
			color: #414959;
			position: relative;
			top: 0px;
			left: 5px;
			font-size: 14px;
		}
	}
	> div {
		width: 100%;
	}
	.line-height {
		height: 40px;
		line-height: 40px;
	}

	.tree-head {
		height: 40px;
		line-height: 40px;
		background: #fff;
		border-bottom: 2px solid #e8e8e8;
		td {
			color: rgba(0, 0, 0, 0.45);
			border-bottom: 2px solid #e8e8e8;
			color: #333;
		}
		.arrow {
			position: relative;
			display: inline-block;
			width: 16px;
			height: 16px;
			background: #fff;
			span {
				position: absolute;
				display: block;
				width: 0;
				height: 0;
				left: 0;
				border-style: solid;
				border-width: 4px;
			}
			.up-arrow {
				top: 3px;
				border-color: transparent transparent #ddd transparent;
				&.sort {
					border-bottom-color: #3c4454;
				}
			}
			.down-arrow {
				top: 12px;
				border-color: #ddd transparent transparent transparent;
				&.sort {
					border-top-color: #3c4454;
				}
			}
		}
	}
	.ellipsis {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	.tree-wrap {
		top: 40px;
		background: #fff;
	}
	.tree-body {
		table {
			table-layout: fixed;
		}
		td {
			height: 40px;
			line-height: 40px;
			color: rgba(0, 0, 0, 0.85);
			font-size: 14px;
			width: 33%;
			.reset {
				color: #1890ff;
			}
			.delete {
				color: #f5222d;
			}
			.line {
				display: inline-block;
				width: 1px;
				background: rgba(0, 0, 0, 0.09);
				margin: 0 11px;
				height: 14px;
			}
		}
		.td-title {
			position: relative;
			a {
				display: block;
			}
			.tree-close,
			.tree-open {
				position: absolute;
				width: 0;
				height: 0;
				border-color: #999;
				border-style: solid;
				cursor: pointer;
				border-width: 5px;
				z-index: 2;
			}
			.tree-close {
				left: -12px;
				top: 14px;
				border-color: transparent transparent transparent #aaaaaa;
			}
			.tree-open {
				left: -17px;
				top: 17px;
				border-color: #aaaaaa transparent transparent;
			}
		}
		.leve-1 .td-title a {
			width: 230px;
		}
		.leve-2 .td-title a {
			width: 200px;
		}
		.leve-3 .td-title a {
			width: 170px;
		}
		.leve-4 .td-title a {
			width: 140px;
		}
		.leve-5 .td-title a {
			width: 110px;
		}
		.leve-6 .td-title a {
			width: 80px;
		}
		.leve-7 .td-title a {
			width: 50px;
		}
		.leve-8 .td-title a {
			width: 20px;
		}
		.other-node {
			position: relative;
			> .not-border:not(:last-child) {
				position: relative;
				.before-line {
					content: '';
					position: absolute;
					width: 1px;
					height: 100%;
					background-color: #e8e8e8;
					border: none;
					top: -20px;
					z-index: 1;
					left: 34px;
				}
			}
		}
		.not-border {
			.td-title {
				&::before {
					content: '';
					position: absolute;
					width: 1px;
					height: 40px;
					background-color: #e8e8e8;
					border: none;
					left: -12px;
					top: -20px;
					z-index: 1;
				}
				&::after {
					content: '';
					left: -11px;
					position: absolute;
					right: auto;
					background-color: #e8e8e8;
					height: 1px;
					top: 19px;
					width: 16px;
					z-index: 1;
				}
			}
		}
	}
	.t-icon {
		display: inline-block;
		width: 14px;
		height: 18px;
		background-size: 100% 100% !important;
		vertical-align: middle;
		margin: -3px 4px 0 0;
		&.m-dep {
			background: url(data:image/png;base64,)
				no-repeat;
		}
	}
}
</style>



